.App-logo {
    height: 4vw;
    padding:2vw;
  }
   
  .App-header {
    background-color: #000000;
    margin-top: 2vw;
    margin-left: 10%;
    margin-right: 10%;
    height: 7vw;
    padding:1.2vw;
    -ms-flex-item-align: center;
        align-self: center;
  }

  #logo{
      position:absolute;
      padding:-2vw;
      float:left;
      margin-left: 1vw;
 
  }

  ul{
      width: auto;
      float:right;
      padding: -2vw;    
      height:4vw;
      margin: none;

  }

  li{
      display: inline-block;
      height:3.5vw;
   
      padding: 2vw;
      
  }

  a{
     text-align: center; 
     color:white;
     text-decoration: none;
     font-family: 'Open Sans', sans-serif;
     font-size: 1.2vw;
     padding-top: -0.5vw;
  }

  a:hover{
      position:initial;
      color:yellow;
      -webkit-transition:0.5s;
      -o-transition:0.5s;
      transition:0.5s;
      font-size: 1.4vw;
  }
  nav{
      padding-top: 2vw;
      
  }
  p{
      margin-bottom: none;
  }
  @media only screen and (max-width: 1000px) {
    .App-header {
        background-color: #000000;
        margin-top: 2vw;
        margin-left: 4vw;
        margin-right: 4vw;
        height: 7vw;
        padding:1.2vw;
        -ms-flex-item-align: center;
            align-self: center;
      }

      ul{
        width: auto;
        float:right;  
        height:4vw;
        margin-top: 1vw;
  
    }
    li{
        display: inline-block;
        height:2vw;
     
        padding: 2vw;
        padding-top: 0vw;
        
    }
    a{
        text-align: center; 
        color:white;
        text-decoration: none;
        font-family: 'Open Sans', sans-serif;
        font-size: 2vw;
        padding-top: -0.5vw;
     }
     a:hover{
        position:initial;
        color:yellow;
        -webkit-transition:0.5s;
        -o-transition:0.5s;
        transition:0.5s;
        font-size: 2.1vw;
    }
  }



